const axios = require('axios');
const rxjs = require('rxjs');
const info = require('./personal.json');

let subject = new rxjs.Subject();
let commandIdx = 0;
const COMMMANDS = [
    'download cv',
    'preview cv',
    'whoami',
    'skills',
    'experience',
    'github',
    'linkedin',
    'clear',
    'exit'
];

const setIndex = (command) => {
    let tmp = COMMMANDS.findIndex((element) => {
        return element === command;
    });
    commandIdx = tmp === -1 ? 0 : tmp;
};

const terminal = (command, callback) => {
    command = command.trim();
    setIndex(command);
    switch (command) {
        case 'download cv':
            downloadCV();
            callback('Downloading CV ...');
            break;
        case 'preview cv':
            openUrl(info.cvUrlOpen);
            callback('Opened CV ...');
            break;
        case 'whoami':
            callback(info.whoami);
            break;
        case 'skills':
            callback('\
            My Skills include:\n\
            - Node.js \n\
            - MongoDB\n\
            - Ionic\n\
            - Angular 2+\n\
            - Vue\n\
            - HTML5\n\
            - CSS3\n\
            - Typescript\n\
            - Git\n\
            - Computer Science\n\
            And a lot more.\n\
            ');
            break;
        case 'experience':
            callback('\
            ----- Full Stack Developer (Feb 2018 - Aug 2019) -----\n\
            (Styloola)\n\
            Tools: Node.js/MongoDB/Postman/Firebase Cloud Messaging/Ionic/Docker \n\
            \n\
            ----- Undergraduate Research Fellow (Sept 2018 - Jun 2019) -----\n\
            Fullstack Developer (ICS-FORTH)\n\
            Tools: Angular,Ionic,Node.js,MongoDB,socket.io,Firebase,Figma,SQLite\n\
            \n\
            ----- Summer Intern (Jul - Aug 2018) -----\n\
            Backend Developer (Omnixell)\n\
            Took part in an 2 month intership in Omnixell and worked as backend\n\
            developer usind Node.js\n\
            Tools: Node.js/MongoDB/Postman/Firebase Cloud Messaging/Ionic\n\
            ');
            break;
        case 'clear':
            callback('clear');
            break;
        case 'exit':
            callback('exit')
            break;
        case '--help':
            callback('\
$ preview cv         --- View my CV letter\n\
$ download cv        --- Download my CV letter\n\
$ whoami             --- Some brief info about myself\n\
$ skills             --- A list of my skills as a programmer\n\
$ projects/github    --- Opening my personal Github account\n\
$ linkedin    --- Opening my personal LinkedIn account\n\
$ clear       --- Clear the console\n\
$ exit        --- Leave the command line\n\
');
            break;
        case 'linkedin':
            openUrl(info.linkedInUrl);
            callback('Opened LinkedIn ...');
            break;
        case 'github':
        case 'projects':
            openUrl(info.githubUrl);
            callback('Opened Github ...');
            break;
        case '':
            callback('');
            break;
        default:
            callback(`${command}: command not found`);
            break;
    }
};

/**
 * https://sites.google.com/site/gdocs2direct/?key5sk1=30c1fdd27dd0c371ccfac0fd5f4c81d8b0e2e543
 * Link for creating 'downloadable' google drive link
 */
const downloadCV = () => {
    return axios({
        url: info.cvUrl,
        method: 'GET',
        responseType: 'blob'
    }).then((response) => {
        subject.next('downloaded');
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', info.downloadFile); //or any other extension
        document.body.appendChild(link);
        link.click();
    });
};


const openUrl = (url) => {
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('target', '_blank'); //or any other extension
    document.body.appendChild(link);
    link.click();
};

const navigateCommands = (index) => {
    commandIdx -= index;
    if (commandIdx >= COMMMANDS.length) commandIdx = COMMMANDS.length - 1;
    if (commandIdx === -1) commandIdx = 0;
    return COMMMANDS[commandIdx];
};

module.exports = {
    terminal,
    event: subject,
    navigateCommands
}