<template>
  <v-container fluid>
    <v-row>
      <sidebar-menu
        @click="collapsed = !collapsed"
        v-click-outside="closeMenu"
        :hideToggle="true"
        :collapsed="collapsed"
        :menu="menu"
        @item-click="onItemClick"
      >
        <div @click="collapsed = !collapsed" class="header" slot="header">
          <img src="../assets/icons/tabIcon.png" />
        </div>
        <div class="footer" slot="footer" v-if="!collapsed">
          Created by
          <a href="www.github/KabitakisGiorgos" target="_blank">Me</a>
        </div>
      </sidebar-menu>
      <v-col class="terminal">
        <v-row>
          <span ref="test" id="startingMessage"></span>
        </v-row>

        <v-row
          class="commandHistory"
          v-for="(command,index) in commands"
          :key="index"
          ref="messageDisplay"
        >
          <span class="prompt">{{command.prompt}}</span>
          <span class="historyText">{{command.text}}</span>
        </v-row>

        <v-row v-if="showPrompt">
          <div class="prompt">{{promptMessage}}</div>
          <input
            ref="terminalInput"
            v-on:blur="setAutoFocus"
            v-click-outside="setAutoFocus"
            class="terminalInput"
            type="text"
            v-on:keydown.13="handleCommand"
            v-on:keydown.38="navigateCommands(-1)"
            v-on:keydown.40="navigateCommands(1)"
            v-model="newCommand"
            :disabled="disabledTerminal"
            autofocus
          />
        </v-row>
        <v-row>
          <span v-if="leave" class="prompt">{{promptMessage}}</span>
          <span class="historyText" id="leaveMessage"></span>
        </v-row>
        <v-row class="blank_line"></v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ClickOutside from "vue-click-outside";
import Typed from "typed.js";
import * as info from "../personal.json";
import * as handler from "../terminal";
import router from "../router";

export default {
  name: "terminal",
  data() {
    return {
      commands: [],
      eventSubscription: null,
      showPrompt: false,
      disabledTerminal: false,
      promptMessage: info.prompt,
      leave: false,
      newCommand: "",
      menu: [
        {
          href: "/",
          title: "Home",
          icon: "fa fa-home"
        },
        {
          title: "Who Am I",
          icon: "fa fa-user"
        },
        {
          title: "Skills",
          icon: "fa fa-cogs"
        },
        {
          title: "Experience",
          icon: "fa fa-history"
        },
        {
          title: "Github",
          icon: "fab fa-github"
        },
        {
          title: "LinkedIn",
          icon: "fab fa-linkedin"
        },
        {
          title: "Download CV",
          icon: "fa fa-file-pdf"
        }
      ],
      collapsed: true
    };
  },
  methods: {
    closeMenu() {
      this.collapsed = true;
    },
    setAutoFocus() {
      if (this.$refs.terminalInput) this.$refs.terminalInput.focus();
    },
    navigateCommands(index) {
      this.newCommand = handler.navigateCommands(index);
    },
    handleCommand() {
      if (!this.disabledTerminal)
        handler.terminal(this.newCommand, result => {
          if (result === "clear") {
            this.commands = [];
            this.newCommand = "";
            return;
          }
          if (result === "Downloading CV ...") {
            this.disabledTerminal = true;
          }
          if (result === "exit") {
            router.push("/");
            return;
          }
          this.newLine(result);
        });
    },
    newLine(text) {
      this.commands.push({
        prompt: info.prompt,
        text: this.newCommand
      });
      this.newCommand = "";
      this.commands.push({
        prompt: "",
        text: text
      });
      setTimeout(() => {
        this.scrollToBottom();
      }, 10);
    },
    scrollToBottom() {
      let elem = this.$el.getElementsByClassName("blank_line")[0];
      elem.scrollIntoView({
        block: "start",
        inline: "end"
      });
    },
    onItemClick(event, item) {
      if (this.leave) return;
      if (item.title === "Download CV") {
        this.newCommand = "download cv";
        this.handleCommand();
        return;
      }
      if (item.title === "Github") {
        this.newCommand = "github";
        this.handleCommand();
        return;
      }
      if (item.title === "LinkedIn") {
        this.newCommand = "linkedin";
        this.handleCommand();
        return;
      }
      if (item.title === "Who Am I") {
        this.newCommand = "whoami";
        this.handleCommand();
        return;
      }
      if (item.title === "Skills") {
        this.newCommand = "skills";
        this.handleCommand();
        return;
      }
      if (item.title === "Experience") {
        this.newCommand = "experience";
        this.handleCommand();
        return;
      }
    }
  },
  directives: {
    ClickOutside
  },
  mounted() {
    new Typed("#startingMessage", {
      strings: [info.startingMessage], //TODO: remove this message on deploy
      typeSpeed: 10,
      showCursor: false,
      onComplete: () => {
        this.showPrompt = true;
      }
    });

    let event = handler.event.asObservable();
    this.eventSubscription = event.subscribe(() => {
      this.commands.splice(this.commands.length, 0, {
        prompt: "",
        text: "Done"
      });
      this.disabledTerminal = false;
    });
  },
  beforeDestroy() {
    this.eventSubscription.unsubscribe();
  },
  beforeRouteLeave(to, from, next) {
    if (!this.leave && this.showPrompt) {
      this.disabledTerminal = true;
      this.leave = true;
      new Typed("#leaveMessage", {
        strings: [info.leaveMessage],
        typeSpeed: 40,
        showCursor: false,
        onComplete: () => {
          setTimeout(() => {
            next();
          }, 1500);
        }
      });
    } else next();
  }
};
</script>

